/* Breakpoint mixins */

@mixin for-mobile-layout {
    @media (max-width: $mobile-breakpoint) {
        @content;
    }
}
@mixin for-w-mobile-layout {
    @media (max-width: $w-mobile-breakpoint) {
        @content;
    }
}

@mixin for-w2-mobile-layout {
    @media (max-width: $w2-mobile-breakpoint) {
        @content;
    }
}

@mixin for-narrow-layout {
    @media (max-width: $narrow-breakpoint) {
        @content;
    }
}

@mixin for-w2-normal-layout {
    @media (max-width: $w2-normal-breakpoint) {
        @content;
    }
}

@mixin for-normal-layout {
    @media (max-width: $normal-breakpoint) {
        @content;
    }
}

@mixin for-wide-layout {
    @media (max-width: $desktop-breakpoint + 80) {
        @content;
    }
}

/* Media  Mixins */
@mixin aspect-ratio($width, $height) {
    position: relative;
    overflow: hidden;
    &:before {
        display: block;
        content: "";
        width: 100%;
        padding-top: ($height / $width) * 100%;
    }
    > * {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    > img {
        height: 100%;
        object-fit: cover;
    }
}

// Mixin for removing hover efects on iphone screen
@mixin hover-supported {    
    @media not all and (pointer: coarse) {
        &:hover {
            @content;
        }
    }
}
